<template>
  <div class="box">
    <LayoutBox one="新增活动">
      <el-button type="warning" size="small" @click.native.prevent="onBargains">秒杀</el-button>
      <el-button type="primary" size="small" @click.native.prevent="onTeamBuy">团购</el-button>
      <div v-show="activeType == 1" class="form_box">
        <h4>活动介绍</h4>
        <el-form ref="bargainsForm" label-width="140px" :model="bargainsForm" :rules="bargainsFormRules" size="mini">
          <el-form-item label="秒杀标题" prop="activeName">
            <el-input v-model="bargainsForm.activeName" maxlength="30" show-word-limit />
          </el-form-item>
          <el-form-item label="秒杀图片" required>
            <el-upload action="123" :http-request="onBargainsUpload" :on-remove="handleRemove" list-type="picture-card" :file-list="bargainsForm.imageUrl" :limit="1">
              <i class="el-icon-plus" />
            </el-upload>
          </el-form-item>
          <el-form-item label="活动报名开始时间" prop="applyStartTime">
            <el-date-picker v-model="bargainsForm.applyStartTime" type="datetime" placeholder="活动报名开始时间" />
          </el-form-item>
          <el-form-item label="活动报名结束时间" prop="applyEndTime">
            <el-date-picker v-model="bargainsForm.applyEndTime" type="datetime" placeholder="活动报名结束时间" />
          </el-form-item>
          <el-form-item label="活动开始时间" prop="startTime">
            <el-date-picker v-model="bargainsForm.startTime" type="datetime" placeholder="活动开始时间" />
          </el-form-item>
          <el-form-item label="活动结束时间" prop="endTime">
            <el-date-picker v-model="bargainsForm.endTime" type="datetime" placeholder="活动结束时间" />
          </el-form-item>
          <el-form-item label="秒杀折扣率" prop="discountRate">
            <el-input v-model="bargainsForm.discountRate" />
            <span class="mark">例：5折，填写 0.5 即可</span>
          </el-form-item>
          <el-form-item label="活动规则" prop="activeDescribe">
            <el-input v-model="bargainsForm.activeDescribe" type="textarea" maxlength="50" show-word-limit />
          </el-form-item>
        </el-form>
      </div>
      <div v-show="activeType == 0" class="form_box">
        <h4>活动介绍</h4>
        <el-form ref="teamBuyForm" label-width="140px" size="mini" :model="teamBuyForm" :rules="teamBuyFormRules">
          <el-form-item label="拼团标题" prop="activeName">
            <el-input v-model="teamBuyForm.activeName" maxlength="30" show-word-limit />
          </el-form-item>
          <el-form-item label="拼团图片" required>
            <el-upload action="123" :http-request="onTeamBuyUpload" :on-remove="handleRemove" list-type="picture-card" :file-list="teamBuyForm.imageUrl" :limit="1">
              <i class="el-icon-plus" />
            </el-upload>
          </el-form-item>
          <el-form-item label="活动报名开始时间" prop="applyStartTime">
            <el-date-picker v-model="teamBuyForm.applyStartTime" type="datetime" placeholder="活动报名开始时间" />
          </el-form-item>
          <el-form-item label="活动报名结束时间" prop="applyEndTime">
            <el-date-picker v-model="teamBuyForm.applyEndTime" type="datetime" placeholder="活动报名结束时间" />
          </el-form-item>
          <el-form-item label="活动开始时间" prop="startTime">
            <el-date-picker v-model="teamBuyForm.startTime" type="datetime" placeholder="活动开始时间" />
          </el-form-item>
          <el-form-item label="活动结束时间" prop="endTime">
            <el-date-picker v-model="teamBuyForm.endTime" type="datetime" placeholder="活动结束时间" />
          </el-form-item>
          <el-form-item label="团购折扣率" prop="discountRate">
            <el-input v-model="teamBuyForm.discountRate" />
            <span class="mark">例：5折，填写 0.5 即可</span>
          </el-form-item>
          <el-form-item label="成团人数" prop="groupPeople">
            <el-input v-model="teamBuyForm.groupPeople" />
          </el-form-item>
          <el-form-item label="活动规则" prop="activeDescribe">
            <el-input v-model="teamBuyForm.activeDescribe" type="textarea" maxlength="50" show-word-limit />
          </el-form-item>
        </el-form>
      </div>
      <div style="text-align: center">
        <el-button v-if="$route.query.id" class="inquire1" type="success" style="width: 80px" size="small" @click="onUpdate">编辑</el-button>
        <el-button v-else class="inquire1" type="success" style="width: 80px" size="small" @click="submitForm">保存</el-button>
        <router-link to="/adminActivityList"><el-button style="width: 80px" size="small">取消</el-button></router-link>
      </div>
    </LayoutBox>
    <!-- 暂时后端没有加进来
    <el-card class="box-card">
      <div class="classification_box">
        <div class="title_box">
          <h4>* 关联商品</h4>
          <el-button type="danger" icon="el-icon-delete" size="small" circle @click="onDelete" />
        </div>
        <div>
          <el-button v-for="(item, i) in childClassificationList" :key="i" class="inquire1" type="info" size="mini" round @click="onChildClassification(i)">
            {{ item.categoryName }}</el-button>
        </div>
        <div class="add" @click="dialogVisible = true">
          <i class="el-icon-plus" />
          <span>添加商品类目</span>
        </div>
        <el-dialog title="添加大类" :visible.sync="dialogVisible" width="50%">
          <classificationCmp ref="classificationCmp" :parent-classification="classification" />
          <span slot="footer" class="dialog-footer">
            <el-button size="small" style="width: 80px" round @click="dialogVisible = false">取 消</el-button>
            <el-button type="primary" size="small" style="width: 80px; margin-left:15px" round @click="save">确 定</el-button>
          </span>
        </el-dialog>
      </div>
      <el-divider />
    </el-card> -->
  </div>
</template>

<script>
import LayoutBox from '@/components/LayoutBox/index'
// import classificationCmp from '@/views/admin_activity/classification_cmp/index'

export default {
  name: 'AddActivity',

  components: {
    LayoutBox,
    // classificationCmp
  },

  data() {
    return {
      type: 0, // 0-平台 1-商家
      activeType: 1, // 秒杀:1, 团购：0
      dialogVisible: false, // 分类的弹窗
      parentId: 0, // 分类联动查询的id
      classification: [], // 分类大类
      childClassificationList: [], // 选择的分类
      childClassificationIndex: '', // 选择删除的下标
      bargainsForm: {
        activeName: '', // 秒杀标题
        applyStartTime: '', // 开始报名时间
        applyEndTime: '', // 结束报名时间
        startTime: '', // 开始时间
        endTime: '', // 结束时间
        discountRate: '', // 秒杀折扣率
        activeDescribe: '', // 活动规则
        imageUrl: [] // 活动图片
      },
      bargainsFormRules: {
        activeName: [
          { required: true, message: '请输入秒杀标题', trigger: 'chang' }
        ],
        applyStartTime: [
          { required: true, message: '请选择开始报名时间', trigger: 'chang' }
        ],
        applyEndTime: [
          { required: true, message: '请选择结束报名时间', trigger: 'chang' }
        ],
        startTime: [
          { required: true, message: '开始时间', trigger: 'chang' }
        ],
        endTime: [
          { required: true, message: '结束时间', trigger: 'chang' }
        ],
        discountRate: [
          { required: true, message: '请输入秒杀折扣率', trigger: 'chang' }
        ],
        activeDescribe: [
          { required: true, message: '请输入活动规则', trigger: 'chang' }
        ]
      },
      teamBuyForm: {
        activeName: '', // 团购标题
        applyStartTime: '', // 开始报名时间
        applyEndTime: '', // 结束报名时间
        startTime: '', // 开始时间
        endTime: '', // 结束时间
        discountRate: '', // 团购折扣率
        activeDescribe: '', // 活动规则
        imageUrl: [], // 团购图片
        groupPeople: '' // 拼团人数
      },
      teamBuyFormRules: {
        activeName: [
          { required: true, message: '请输入团购标题', trigger: 'chang' }
        ],
        applyStartTime: [
          { required: true, message: '请选择开始报名时间', trigger: 'chang' }
        ],
        applyEndTime: [
          { required: true, message: '请选择结束报名时间', trigger: 'chang' }
        ],
        startTime: [
          { required: true, message: '开始时间', trigger: 'chang' }
        ],
        endTime: [
          { required: true, message: '结束时间', trigger: 'chang' }
        ],
        discountRate: [
          { required: true, message: '请输入团购折扣率', trigger: 'chang' }
        ],
        activeDescribe: [
          { required: true, message: '请输入活动规则', trigger: 'chang' }
        ],
        groupPeople: [
          { required: true, message: '请输入拼团人数', trigger: 'chang' }
        ]
      }
    }
  },

  methods: {
    // 秒杀
    onBargains() {
      this.$refs.bargainsForm.resetFields()
      this.activeType = 1
    },
    // 团购
    onTeamBuy() {
      this.$refs.teamBuyForm.resetFields()
      this.activeType = 0
    },
    // 点击确认
    save() {
      this.childClassificationList = this.$refs.classificationCmp.childClassificationList
      this.dialogVisible = false
    },
    // 选择删除的分类
    onChildClassification(i) {
      this.childClassificationIndex = i
    },
    // 删除分类
    onDelete() {
      this.childClassificationList.splice(this.childClassificationIndex, 1)
    },
    // 秒杀图片上传
    onBargainsUpload(params) {
      this._uploadSectionFile(params)
        .then((res) => {
          this.bargainsForm.imageUrl = [{ url: res }]
        })
    },
    // 团购图片上传
    onTeamBuyUpload(params) {
      this._uploadSectionFile(params)
        .then((res) => {
          this.teamBuyForm.imageUrl = [{ url: res }]
        })
    },
    // 保存
    submitForm() {
      switch (this.activeType) {
        case (0): {
          let data = this._submitForm('teamBuyForm')
          if (data) {
            this.apiAddActivity(data)
          }
          break
        }
        case (1): {
          let data = this._submitForm('bargainsForm')
          if (data) {
            this.apiAddActivity(data)
          }
          break
        }
      }
    },
    // 编辑
    onUpdate () {
      switch (this.activeType) {
        case (0): {
          let data = this._submitForm('teamBuyForm')
          if (data) {
            this.apiUpdateActivity(data)
          }
          break
        }
        case (1): {
          let data = this._submitForm('bargainsForm')
          if (data) {
            this.apiUpdateActivity(data)
          }
          break
        }
      }
    },
    // 图片删除
    handleRemove () {
      switch (this.activeType) {
        case (0): {
          this.teamBuyForm.imageUrl = []
          break
        }
        case (1): {
          this.bargainsForm.imageUrl = []
          break
        }
      }
    },

    //  private 私有方法 ---------------------------------------
    // 表单验证
    _submitForm(formData) {
      let data = false
      this.$refs[formData].validate((valid) => {
        if (valid) {
          data = Object.assign({}, this[formData])
          if (data.imageUrl.length === 0) {
            this.$message.error('请选择图片')
            data = false
            return false
          }
          data.type = this.type // 0-平台 1-商家
          data.activeType = this.activeType
          data.imageUrl = data.imageUrl[0].url
          data.rel = this.childClassificationList // 商品分类
          // eslint-disable-next-line no-return-assign
          data.rel.map((item) => item.categoryId = item.id)
          return data
        } else {
          data = false
          return false
        }
      })
      return data
    },

    // api 接口----------------------------------------------------------------
    // 分类联动查询
    _findByParentId() {
      this.$http.post('/woho/product/category/findByParentId/' + this.parentId)
        .then((res) => {
          this.classification = res.data
        })
    },
    // 新增活动
    apiAddActivity(data) {
      console.log(data)
      this.$http.post('/woho/activity/manage/insert', data)
        .then((res) => {
          console.log(res)
          if (res.data.success) {
            this.$message.success('活动添加成功')
            this.$router.push('/adminActivityList')
          } else {
            this.$message.error(res.data.msg)
          }
        })
    },
    // 编辑活动
    apiUpdateActivity(data) {
      console.log(data)
      this.$http.post('/woho/activity/manage/update', data)
        .then((res) => {
          console.log(res)
          if (res.data.success) {
            this.$message.success('活动编辑成功')
            this.$router.push('/adminActivityList')
          } else {
            this.$message.error(res.data.msg)
          }
        })
    },
    // 上传图片
    _uploadSectionFile(params) {
      const isJPG = params.file.type === 'image/jpeg' || params.file.type === 'image/png'
      const isLt2M = params.file.size / 1024 / 1024 < 10
      if (!isJPG) {
        this.$message.error('上传图片只能是 JPG/PNG 格式!')
        return
      }
      if (!isLt2M) {
        this.$message.error('上传图片大小不能超过 10MB!')
        return
      }
      const formData = new FormData()
      formData.append('file', params.file)
      // this._upload(formData)
      return this.$http.post('/common/oss/uploadFile', formData)
        .then((res) => {
          return res.data.data.webUrl
        })
    },
    // 根据主键id查询活动
    apiFindByIdActivity () {
      this.$http.post(`/woho/activity/manage/findById/${this.$route.query.id}`)
        .then((res) => {
          if (res.data.activeType === 0) {
            this.activeType = 0
            let teamBuyForm = JSON.stringify(res.data)
            teamBuyForm = JSON.parse(teamBuyForm)
            teamBuyForm.imageUrl = []
            teamBuyForm.imageUrl.push({ url: res.data.imageUrl })
            this.teamBuyForm = teamBuyForm
          } else if (res.data.activeType === 1) {
            this.activeType = 1
            let bargainsForm = JSON.stringify(res.data)
            bargainsForm = JSON.parse(bargainsForm)
            bargainsForm.imageUrl = []
            bargainsForm.imageUrl.push({ url: res.data.imageUrl })
            this.bargainsForm = bargainsForm
          }
        })
    },
  },

  created() {
    this._findByParentId()
    if (this.$route.query.id) {
      this.apiFindByIdActivity()
    }
  },
}
</script>

<style lang="scss" scoped>
  $themColor: #5bd9be;

  .box{
    min-width: 1100px;
    .mark{
      color: #ccc;
    }
    .form_box{
      max-width: 640px;
      margin: 44px auto auto auto;
    }
    .box-card{
      margin-top: 15px;
      .classification_box{
        margin: auto;
        width: 654px;
        .title_box{
          display: flex;
          justify-content: space-between;
          align-items: center;
        }
        .add{
          padding-top: 30px;
          color: $themColor;
          text-align: center;
          margin: auto;
        }
      }
    }
  }
</style>
